* {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
}

body {
  letter-spacing: 1.2px;
  background: #000107;
  font-family: Roboto Mono, monospace;
  font-size: 1em;
  line-height: 1;
}

h1, h2, h3, h4, h5 {
  letter-spacing: .4px;
}

a {
  color: #1e90ff;
  text-decoration: none;
}

a:hover {
  color: #00bfff;
  text-decoration: none;
}

.hidden {
  display: none;
}

.gameover {
  color: #ff0107 !important;
}

#header {
  height: 90px;
}

#main {
  display: flex;
}

#board {
  margin: auto;
}

.canvas-wrapper {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  padding: 20px;
}

#canvas {
  cursor: url("circle3.5deea94e.cur") 8 8, auto;
}

#information {
  color: #f1f7ff;
  flex-flow: wrap;
  justify-content: space-evenly;
  padding: 0 20px;
  display: flex;
}

.stats {
  padding: 4px;
}

.stats-label {
  color: #abb1ba;
  font-size: 20px;
  line-height: 1.5;
}

.stats-value {
  font-size: 30px;
}

#healthbar-wrapper {
  height: 55px;
  margin: 0 20px;
}

#healthbar {
  height: 100%;
}

#settings {
  border: 1px solid #f5f5f5;
  margin-top: 20px;
  padding: 20px;
}

#settings input {
  width: 80%;
  color: #f1f7ff;
  background: rgba(241, 247, 255, .1);
  border: 0;
  outline: 0;
  padding: 0;
  font-family: monospace;
  font-size: 30px;
  line-height: 1;
}

#settings button {
  color: #000107;
  cursor: pointer;
  background: #f1f7ff;
  border: 0;
  outline: 0;
  margin: 5px;
  padding: 0;
  font-family: sans-serif;
  font-size: 30px;
  font-weight: 600;
  line-height: 1;
}

.params {
  display: flex;
}

.param-cell {
  width: 25%;
}

.param-label {
  color: #abb1ba;
  font-size: 16px;
  line-height: 1.5;
}

.debug-mode {
  padding-top: 15px;
}

@media screen and (max-width: 445px) {
  #header {
    height: 60px;
  }

  #healthbar {
    margin: 0 10px;
  }

  .canvas-wrapper {
    padding: 10px;
  }

  #information {
    padding: 0 10px;
  }

  .stats-label {
    font-size: 10px;
  }

  .stats-value {
    font-size: 20px;
  }
}

@media screen and (min-width: 1024px) {
  .canvas-wrapper {
    padding: 20px;
  }
}

